

body {
position: absolute;
top: 0;
right: 0;
bottom: 0;
left: 0;
width: auto;
height: auto;
overflow-y: auto;
overflow-x: hidden;
z-index: 1;
}



#app {
position: relative;
top: 0;
right: 0;
bottom: 0;
left: 0;
width: auto;
height: auto;
overflow-y: auto;
overflow-x: hidden;
z-index: 1;
}
.main {
-webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.4);
        box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.4);
background: #eeeeee;
}

.spinner {
width: 250px;
height: 250px;
margin: 0 auto;
-webkit-transform: translate(0%,120%);
    -ms-transform: translate(0%,120%);
        transform: translate(0%,120%);

}
.spinner:after {
content: "";
display: block;
clear: both;
background-color: rgb(63, 113, 202);
background-image: url(Picture2.png);
background-size: contain;
background-repeat: no-repeat;
-webkit-animation: spin 1s linear 1s forwards;
        animation: spin 1s linear 1s forwards;
width: 100px;
height: 100px;
background-position: center;
margin: -50px -34px 0 auto;
-webkit-animation-fill-mode: forwards;
        animation-fill-mode: forwards;
opacity: 0;
-webkit-box-shadow: inset 0px 0px 20px 0px rgb(63, 113, 202), 3px 3px 8px 0px rgba(0, 0, 0, 0.4);
        box-shadow: inset 0px 0px 20px 0px rgb(63, 113, 202), 3px 3px 8px 0px rgba(0, 0, 0, 0.4);

border-radius: 100%;


}
.spinner:before {
content: "";
display: block;
clear: both;
background-color: rgba(36, 65, 116, 1);
background-image: url(Picture1.png);
background-size: contain;
background-repeat: no-repeat;
-webkit-animation: fade 1s linear 0s forwards;
        animation: fade 1s linear 0s forwards;
width: 250px;
height: 250px;
background-position: center;
margin: 0 auto;
border: 0px solid;
border-radius: 100%;
-webkit-animation-fill-mode: forwards;
        animation-fill-mode: forwards;
opacity: 0;
-webkit-box-shadow: inset 0px 0px 20px 0px rgb(36, 65, 116), 3px 3px 8px 0px rgba(0, 0, 0, 0.4);
        box-shadow: inset 0px 0px 20px 0px rgb(36, 65, 116), 3px 3px 8px 0px rgba(0, 0, 0, 0.4);
border-radius: 100%;


}
@-webkit-keyframes spin {
0% { 
opacity: 0;
-webkit-transform: rotate(0deg) scale(0.1);
        transform: rotate(0deg) scale(0.1);
}
40% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
55% { 
opacity: 1;
-webkit-transform: rotate(350deg) scale(1.0);
        transform: rotate(350deg) scale(1.0);
}
70% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
85% { 
opacity: 1;
-webkit-transform: rotate(350deg) scale(1.0);
        transform: rotate(350deg) scale(1.0);
}
100% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
}
@keyframes spin {
0% { 
opacity: 0;
-webkit-transform: rotate(0deg) scale(0.1);
        transform: rotate(0deg) scale(0.1);
}
40% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
55% { 
opacity: 1;
-webkit-transform: rotate(350deg) scale(1.0);
        transform: rotate(350deg) scale(1.0);
}
70% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
85% { 
opacity: 1;
-webkit-transform: rotate(350deg) scale(1.0);
        transform: rotate(350deg) scale(1.0);
}
100% { 
opacity: 1;
-webkit-transform: rotate(360deg) scale(1.0);
        transform: rotate(360deg) scale(1.0);
}
}
@-webkit-keyframes fade {
0% { 
opacity: 0;
-webkit-transform: scale(1);
        transform: scale(1);
}
50% { 
opacity: 0.6;
-webkit-transform: scale(1.1);
        transform: scale(1.1);
}
100% { 
opacity: 1;
-webkit-transform: scale(1);
        transform: scale(1);
}
}
@keyframes fade {
0% { 
opacity: 0;
-webkit-transform: scale(1);
        transform: scale(1);
}
50% { 
opacity: 0.6;
-webkit-transform: scale(1.1);
        transform: scale(1.1);
}
100% { 
opacity: 1;
-webkit-transform: scale(1);
        transform: scale(1);
}
}
